<template>
  <div class="lib-container">
    <h2>系统公告</h2>
    <div class="news-list">
      <CardItem v-for="item in list" :key="item.datetime">
        <h3>
          <el-tag :type="type[item.type].style">{{
            type[item.type].label
          }}</el-tag
          >{{ item.title }}
        </h3>
        <div v-html="item.msg"></div>
        <p class="date">{{ item.datetime }}</p>
      </CardItem>
    </div>
  </div>
</template>
<script>
import CardItem from "@/components/comp/card-item.vue";
export default {
  components: { CardItem },
  data() {
    return {
      list: [],
      type: {
        update: {
          label: "迭代更新",
          style: "success",
        },
        others: {
          label: "QA",
          style: "error",
        },
      },
    };
  },
  mounted() {
    this.getDatas();
  },
  methods: {
    getDatas() {
      const _this = this;
      this.$axios.get(`./news.json?v=${Math.random()}`).then((res) => {
        let list = res.data.news.filter((news) => news.show);
        _this.list = list;
      });
    },
  },
};
</script>

