  h3 {
    font-size: 25px;
    text-align: center;
    span {
      margin-right: 10px;
    }
  }
  div,
  p {
    font-size: 18px;
    &.date {
      text-align: right;
      margin-top: 20px;
    }
  }
  ::v-deep img {
    max-width: 100% !important;
    display: block;
